import {useRef, useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'

import {
  Svg as SvgLayout,
  ArrowUp,
  ArrowDown,
  LineText,
  LineRect
} from './layout'
import {store} from 'store'

const SvgBase = ({onDirty}) => {
  const {manage_room, lang} = store
  const {size, contain, level_ceil, level_floor} = manage_room
  const {img, offset} = contain
  const {x, y} = offset
  const {src} = img
  const {width, height} = img.size
  const [ceil_hover, setCeilHover] = useState(false)
  const [floor_hover, setFloorHover] = useState(false)
  const [is_ceil, setIsCeil] = useState(false)
  const [is_floor, setIsFloor] = useState(false)
  const [mouse, setMouse] = useState({y: 0})
  const [ceil, setCeil] = useState(level_ceil)
  const [floor, setFloor] = useState(level_floor)
  const [move, setMove] = useState()
  const [up, setUp] = useState(0)

  useEffect(() => {
    const mouse_move = e => setMove(e.clientY)
    let counter = 1
    const mouse_up = () => setUp(counter++) 
    document.body.addEventListener('mousemove', mouse_move)
    document.body.addEventListener('mouseup', mouse_up)
    return () => {
      document.body.removeEventListener('mousemove', mouse_move)
      document.body.removeEventListener('mouseup', mouse_up)
    }
  }, [])

  useEffect(() => {
    if(!is_ceil && !is_floor) return
    onDirty()
    const delta = move - mouse.y
    if(is_ceil) {
      let res = level_ceil + delta
      if(res <= 0) res = 0
      if(res >= contain.img.size.height) res = contain.img.size.height
      setCeil(res)
    }
    if(is_floor) {
      let res = level_floor + delta
      if(res <= 0) res = 0
      if(res >= contain.img.size.height) res = contain.img.size.height
      setFloor(res)
    }
  }, [move])

  useEffect(() => {
    if(!is_ceil && !is_floor) return
    manage_room.set_level_ceil(ceil)
    manage_room.set_level_floor(floor)
    setMouse()
    setIsCeil(false)
    setIsFloor(false)
  }, [up])

  useEffect(() => {
    setCeil(level_ceil)
  }, [level_ceil])

  useEffect(() => {
    setFloor(level_floor)
  }, [level_floor])

  const ceil_down = e => {
    setMouse({y: e.clientY})
    setIsCeil(true)
  }

  const ceil_over = () => {
    setCeilHover(true)
  }

  const ceil_out = () => {
    setCeilHover(false)
  }

  const floor_down = e => {
    setMouse({y: e.clientY})
    setIsFloor(true)
  }

  const floor_over = () => {
    setFloorHover(true)
  }

  const floor_out = () => {
    setFloorHover(false)
  }

  const is_ceil_hover = ceil_hover || is_ceil
  const is_floor_hover = floor_hover || is_floor

  return (
    <SvgLayout
      width={size.width}
      height={size.height}
      viewBox={`0 0 ${size.width} ${size.height}`}
      xmlns="http://www.w3.org/2000/svg"
    >
      <image href={src} x={x} y={y} width={width} height={height} />
      <g transform={`translate(0, ${ceil})`}>
        {is_ceil_hover && (<ArrowUp x={x + width / 2 - 8} y={y - 38} />)}
        <line x1={x} y1={y} x2={x + width} y2={y} stroke="#EF5D54" strokeWidth="2" />
        <rect x={x + width / 2 - 33} y={y - 14} width="66" height="28" fill="#EF5D54" rx="14" ry="14" />
        <LineText x={x + width / 2} y={y + 4} textAnchor="middle">{lang.mr.ceil}</LineText>
        <LineRect onMouseDown={ceil_down} onMouseOver={ceil_over} onMouseOut={ceil_out} x={x + width / 2 - 33} y={y - 14} width="66" height="28" />
        {is_ceil_hover && (<ArrowDown x={x + width / 2 - 8} y={y + 22} />)}
      </g>
      <g transform={`translate(0, ${floor})`}>
        {is_floor_hover && (<ArrowUp x={x + width / 2 - 8} y={y - 38} />)}
        <line x1={x} y1={y} x2={x + width} y2={y} stroke="#EF5D54" strokeWidth="2" />
        <rect x={x + width / 2 - 33} y={y - 14} width="66" height="28" fill="#EF5D54" rx="14" ry="14" />
        <LineText x={x + width / 2} y={y + 4} textAnchor="middle">{lang.mr.floor}</LineText>
        <LineRect onMouseDown={floor_down} onMouseOver={floor_over} onMouseOut={floor_out} x={x + width / 2 - 33} y={y - 14} width="66" height="28" />
        {is_floor_hover && (<ArrowDown x={x + width / 2 - 8} y={y + 22} />)}
      </g>
      <rect x={0} y={0} width={size.width} height={y} fill="#3D3E47" />
      <rect x={0} y={y + height} width={size.width} height={y} fill="#3D3E47" />
    </SvgLayout>
  )
}

export const Svg = observer(SvgBase)
