import {useState, useEffect} from 'react'
import styled from 'styled-components'

const Container = styled.div`
  height: 52px;
  border-radius: 12px;
  background: #EDEDED;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
`

const rich_input = ({rich}) => rich ? `
  margin: 22px 12px 6px;
  height: 24px;
  line-heigth: 24px;
` : `
  margin: 8px 12px;
  height: 36px;
  line-heigth: 36px;
`

const InputEl = styled.input`
  grid-column: 1;
  grid-row: 1;
  width: calc(100% - 24px);
  ${rich_input}
  border: none;
  font-family: Roboto;
  font-weight: normal;
  font-size: 15px;
  color: #202020;
  background: #EDEDED;
  outline: none;

  &::placeholder {
    color: #676767;
  }
`

const Label = styled.div`
  grid-column: 1;
  grid-row: 1;
  margin-top: 6px;
  margin-left: 12px;
  height: 16px;
  font-family: Roboto;
  font-weight: normal;
  font-size: 11px;
  line-height: 16px;
  color: #676767;
`

export const Input = ({className, value, placeholder, onChange}) => {
  const [focused, setFocused] = useState(false)
  const [is_rich, setIsRich] = useState(false)

  useEffect(() => {
    const is_value = !isNaN(parseFloat(value))
    setIsRich(focused || is_value)
  }, [focused, value])

  return (
    <Container className={className}>
      {is_rich && (<Label>{placeholder}</Label>)}
      <InputEl
        value={value}
        onChange={onChange}
        type="number"
        min="0"
        max="2000"
        placeholder={is_rich ? '' : placeholder}
        rich={is_rich}
        onFocus={() => setFocused(true)}
        onBlur={() => setFocused(false)}
      />
    </Container>
  )
}
