import {useState} from 'react'
import styled from 'styled-components'

import dropdown_svg from 'assets/dropdown.svg'

const Container = styled.div`
  height: 52px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
`

const Body = styled.div`
  grid-row: 1;
  grid-column: 1;
  height: 52px;
  width: 100%;
  border-radius: 12px;
  background: #EDEDED;
  display: flex;
  justify-content: stretch;
  align-items: center;
  cursor: pointer;
`

const Value = styled.div`
  margin-left: 12px;
  flex: 1 0;
  font-family: Roboto;
  font-weight: normal;
  font-size: 15px;
  line-height: 52px;
`

const Down = styled.div`
  margin-right: 12px;
  width: 24px;
  height: 24px;
  background-image: url(${dropdown_svg});
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px 24px;
`

const Options = styled.div`
  grid-row: 1;
  grid-column: 1;
  width: 100%;
  margin-top: 52px;
  border: 1px solid #E6E6E6;
  box-sizing: border-box;
  box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  display: flex;
  justify-content: stretch;
  background: #FCFCFC;
  z-index: 1;
`

const List = styled.div`
  margin: 14px 0;
  flex: 1 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
`

const Option = styled.div`
  height: 38px;
  font-family: Roboto;
  font-weight: normal;
  font-size: 15px;
  line-height: 38px;
  padding-left: 16px;
  cursor: pointer;

  &:hover {
    background: #EDEDED;
  }
`

export const Dropdown = ({className, options, value, onChange}) => {
  const [open, setOpen] = useState(false)

  const onOptionClick = option => {
    setOpen(false)
    onChange(option)
  }

  return (
    <Container className={className}>
      <Body onClick={() => setOpen(!open)}>
        <Value>{value}</Value>
        <Down />
      </Body>
      {open && (
        <Options>
          <List>
            {options.map(option => (
              <Option key={`option_${option}`} onClick={() => onOptionClick(option)}>{option}</Option>
            ))}
          </List>
        </Options>
      )}
    </Container>
  )
}
