import {useState} from 'react'
import {observer} from 'mobx-react-lite'

import {
  Container,
  Modal,
  Header,
  Title,
  Img,
  Help,
  HeightBlock,
  CloseBtn,
  DoneBtn,
  Footer,
  Input,
  Dropdown
} from './layout'
import {store} from 'store'

const measures = ['cm', 'in']

const HeightModalBase = ({onClose, onDone}) => {
  const {lang, measure} = store
  let delta = measure === 'cm' ?
      store.manage_room.level.cm_delta :
      store.manage_room.level.inch_delta
  if(!delta) delta = ''
  const [height, setHeight] = useState(delta)

  const is_nan = isNaN(parseFloat(height))

  const measure_change = val => {
    store.set_measure(val)
    let delta = val === 'cm' ?
        store.manage_room.level.cm_delta :
        store.manage_room.level.inch_delta

    setHeight(delta || '')
  }

  const on_done = () => {
    if(is_nan) return
    onDone(height)
  }

  return (
    <Container>
      <Modal>
        <Header>
          <Title>{lang.mr.modal.title}</Title>
          <CloseBtn onClick={onClose} />
        </Header>
        <Img />
        <Help>{lang.mr.modal.help}</Help>
        <HeightBlock>
          <Input
            placeholder={lang.mr.modal.placeholder}
            value={height}
            onChange={e => setHeight(e.target.value)}
          />
          <Dropdown
            options={measures}
            value={measure}
            onChange={measure_change}
          />
        </HeightBlock>
        <Footer>
          <DoneBtn disabled={is_nan} onClick={on_done}>{lang.mr.modal.done}</DoneBtn>
        </Footer>
      </Modal>
    </Container>
  )
}

export const HeightModal = observer(HeightModalBase)
