import {
  Container,
  Item,
  EditIcon,
  PicIcon,
  RemoveIcon,
  Text
} from './layout'
import {store} from 'store'

export const Submenu = ({onEdit, onRemove, onPic, className}) => {
  const {lang} = store, {main} = lang, {submenu} = main

  return (
    <Container className={className}>
      <Item onClick={onEdit}><EditIcon /><Text>{submenu.edit}</Text></Item>
      <Item onClick={onRemove}><RemoveIcon /><Text>{submenu.remove}</Text></Item>
      <Item onClick={onPic}><PicIcon /><Text>{submenu.pic}</Text></Item>
    </Container>
  )
}
