export const Image = ({source, blur}) => {
  if(!source) return null

  const {offset, img} = source
  const {x, y} = offset
  const {src, size} = img
  const {width, height} = size

  const filter = blur ? 'url(#blur)' : ''

  return (
    <image filter={filter} href={src} x={x} y={y} width={width} height={height} />
  )
}

Image.defaultProps = {
  blur: false
}
