import {useState} from 'react'
import {observer} from 'mobx-react-lite'

import {store} from 'store'
import {
  Back,
  ImgButton,
  CloseButton,
  Text,
  AddPic,
  AddButton,
  ChoosePic,
  ChooseButton
} from './layout'

const BottomMenuBase = ({onAdd, onChoose}) => {
  const [menu, setMenu] = useState(false)
  const {lang: {mob: {bottom}}} = store

  return (
    <>
      <ImgButton onClick={() => setMenu(true)} />
      {menu && (
        <Back>
          <AddPic><Text>{bottom.add}</Text><AddButton onClick={onAdd} /></AddPic>
          <ChoosePic><Text>{bottom.choose}</Text><ChooseButton onClick={onChoose} /></ChoosePic>
          <CloseButton onClick={() => setMenu(false)} />
        </Back>
      )}
    </>
  )
}

export const BottomMenu = observer(BottomMenuBase)
