import {useState, useRef} from 'react'
import {observer} from 'mobx-react-lite'

import {store} from 'store'
import {
  Container,
  Left,
  Right,
  GaleryBtn,
  DownloadBtn,
  MoreBtn,
  ShareBtn,
  MainBtn,
  File
} from './layout'
import {Tooltip} from './Tooltip'

const BottomMenuBase = ({
  onGalery, onMain, onDownload, onShare, onMore
}) => {
  const [is_tooltip, setIsTooltip] = useState(true)
  const fileRef = useRef()
  const {lang} = store, {main} = lang

  const openFile = () => {
    if(!fileRef || !fileRef.current) return
    fileRef.current.click()
  }

  const fileChange = fe => {
    const {files} = fe.target
    if(!files || files.length < 1) return
    const reader = new FileReader()
    reader.onload = re => {
      const {result} = re.target 
      if(!result) return
      onMain(result)
      fileRef.current.value = null
    }
    reader.readAsDataURL(fe.target.files[0])
  }

  const close_tooltip = () => {
    setIsTooltip(false)
  }

  return (
    <>
      <Container>
        <File ref={fileRef} onChange={fileChange} />
        <Left>
          <GaleryBtn akcent onClick={onGalery} />
        </Left>
        <MainBtn onClick={openFile}>{main.main_btn}</MainBtn>
        <Right>
          <DownloadBtn onClick={onDownload} />
          <ShareBtn onClick={onShare} />
          <MoreBtn onClick={onMore} />
        </Right>
      </Container>
      {is_tooltip && (<Tooltip onClose={close_tooltip} />)}
    </>
  )
}

export const BottomMenu = observer(BottomMenuBase)
