import {useState, useEffect} from 'react'
import {observer} from 'mobx-react-lite'

import {
  Container,
  Modal,
  Header,
  Title,
  CloseBtn,
  Help,
  SizeBlock,
  WidthInput,
  HeightInput,
  Dropdown,
  Footer,
  DoneBtn
} from './layout'
import {store} from 'store'

const measures = ['cm', 'in']

const PictureSettingsBase = ({size, onClose, onDone}) => {
  const {lang, measure} = store
  const {pic} = lang.main
  const [height, setHeight] = useState()
  const [width, setWidth] = useState()
  const [rel, setRel]  = useState(size.width / size.height)
  const [is_valid, setIsValid] = useState(true)

  useEffect(() => {
    setRel(size.width / size.height)
  }, [size])

  useEffect(() => {
    const width_is_nan = isNaN(parseFloat(width))
    const height_is_nan = isNaN(parseFloat(height))
    setIsValid(!width_is_nan && !height_is_nan && width !== 0 && height !== 0)
  }, [width, height])

  useEffect(() => {
    if(measure === 'cm') {
      setWidth(size.width)
      setHeight(size.height)
    }
    if(measure === 'in') {
      const in_w = Math.round((size.width / 2.54 + Number.EPSILON) * 100) / 100
      const in_h = Math.round((size.height / 2.54 + Number.EPSILON) * 100) / 100
      setWidth(in_w)
      setHeight(in_h)
    }
  }, [measure])

  const measure_change = val => {
    store.set_measure(val)
  }

  const height_change = e => {
    const value = parseFloat(e.target.value)
    if(isNaN(value)) {
      setHeight('')
      setWidth('')
      return
    }
    setHeight(value)
    const alt = Math.round((value * rel + Number.EPSILON) * 100) / 100
    setWidth(alt)
  }

  const width_change = e => {
    const value = parseFloat(e.target.value)
    if(isNaN(value)) {
      setHeight('')
      setWidth('')
      return
    }
    setWidth(value)
    const alt = Math.round((value / rel + Number.EPSILON) * 100) / 100
    setHeight(alt)
  }

  const done_click = () => {
    if(!is_valid) return
    if(measure === 'cm') {
      onDone({width, height})
    }
    if(measure === 'in') {
      const cm_w = Math.round((width * 2.54 + Number.EPSILON) * 100) / 100
      const cm_h = Math.round((height * 2.54 + Number.EPSILON) * 100) / 100
      onDone({width: cm_w, height: cm_h})
    }
  }

  return (
    <Container>
      <Modal>
        <Header>
          <Title>{pic.title}</Title>
          <CloseBtn onClick={onClose} />
        </Header>
        <Help>{pic.help}</Help>
        <SizeBlock>
          <WidthInput
            placeholder={pic.width}
            value={width}
            onChange={width_change}
          />
          <HeightInput
            placeholder={pic.height}
            value={height}
            onChange={height_change}
          />
          <Dropdown
            options={measures}
            value={measure}
            onChange={measure_change}
          />
        </SizeBlock>
        <Footer><DoneBtn disabled={!is_valid} onClick={done_click}>{pic.done}</DoneBtn></Footer>
      </Modal>
    </Container>
  )
}

export const PictureSettings = observer(PictureSettingsBase)
