import {useState, useEffect, useRef} from 'react'
import {observer} from 'mobx-react-lite'

import {Container} from './Container'
import {Submenu, Tooltip, File} from './layout'
import {Svg} from './Svg'
import {TopMenu} from 'components/TopMenu'
import {BottomMenu} from 'components/BottomMenu'
import {ManageRoom} from 'components/ManageRoom'
import {PictureSettings} from 'components/PictureSettings'
import {Galery} from 'components/Galery'
import {store} from 'store'

const DesktopAppBase = () => {
  const fileRef = useRef()
  const {manage_room, lang} = store
  const {main} = lang
  const [is_more, setIsMore] = useState(false)
  const [is_removed, setIsRemoved] = useState(false)
  const [pic, setPic] = useState()
  const [is_galery, setIsGalery] = useState(false)

  useEffect(() => {
    const cur_lang = new URLSearchParams(document.location.search).get('lang')
    if(cur_lang) {
      store.set_lang(cur_lang)
    }
  }, [])

  useEffect(() => {
    const hnd = () => {
      if(is_more) setIsMore(false)
    }
    document.body.addEventListener('click', hnd)

    return () => {
      document.body.removeEventListener('click', hnd)
    }
  }, [is_more])

  useEffect(() => {
    if(!is_removed) return
    setTimeout(() => {
      setIsRemoved(false)
    }, 5000)
  }, [is_removed])

  const onMain = res => {
    manage_room.setForLevel(res)
  }

  const onMore = e => {
    e.stopPropagation()
    setIsMore(!is_more)
  }

  const edit_room = () => {
    store.manage_exist_room()
  }

  const remove_room = () => {
    store.set_desktop_default(() => {
      store.recalc()
      setIsRemoved(true)
    })
  }

  const upload_pic = () => {
    if(!fileRef || !fileRef.current) return
    fileRef.current.click()
  }

  const fileChange = fe => {
    const {files} = fe.target
    if(!files || files.length < 1) return
    const reader = new FileReader()
    reader.onload = re => {
      const {result} = re.target 
      if(!result) return
      store.upload_pic(result)
      fileRef.current.value = null
    }
    reader.readAsDataURL(files[0])
  }

  const close_pic = () => {
    setPic()
  }

  const done_pic = size => {
    store.resize_pic(pic, size)
    setPic()
  }

  const open_galery = () => {
    setIsGalery(true)
  }

  const close_galery = () => {
    setIsGalery(false)
  }

  const choose_picture = picture => {
    store.select_from_galery(picture)
  }

  return (
    <Container>
      <File ref={fileRef} onChange={fileChange} />
      {is_removed && <Tooltip>{main.remove_tooltip}</Tooltip>}
      <Svg onSize={picture => setPic(picture)} />
      <TopMenu />
      <BottomMenu onMain={onMain} onMore={onMore} onGalery={open_galery} />
      {is_more && <Submenu onEdit={edit_room} onRemove={remove_room} onPic={upload_pic} />}
      {manage_room.img.is_valid && <ManageRoom />}
      {pic && <PictureSettings size={pic.cm_size} onClose={close_pic} onDone={done_pic} />}
      {is_galery && <Galery onClose={close_galery} onChoose={choose_picture} />}
    </Container>
  )
}

export const DesktopApp = observer(DesktopAppBase)
