import {useRef, useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'

import {
  Back,
  Help,
  Container,
  TopMenu,
  CloseBtn,
  MenuTitle,
  NextBtn,
  NextBtnText,
  NextBtnIcon
} from './layout'
import {Svg} from './Svg'
import {HeightModal} from './Modal'
import {store} from 'store'

const ManageRoomBase = () => {
  const ref = useRef()
  const [is_help, setIsHelp] = useState(true)
  const [is_height, setIsHeight] = useState(false)
  const {manage_room, interior, lang, measure} = store
  const [resize, setResize] = useState({})

  useEffect(() => {
    const handler = () => {
      setResize({})
    }
    window.addEventListener('resize', handler)
    return () => window.removeEventListener('resize', handler)
  }, [])

  useEffect(() => {
    if(!ref || !ref.current) return
    const container = ref.current
    const rect = container.getBoundingClientRect();
    manage_room.set_level_size(rect.width, rect.height)
  }, [ref, resize])

  const on_dirty = () => {
    setIsHelp(false)
  }

  const on_close = () => {
    manage_room.clear()
  }

  const on_next = () => {
    setIsHeight(true)
  }

  const on_height_close = () => setIsHeight(false)

  const on_height_done = value => {
    setIsHeight(false)
    let delta = parseFloat(value)
    if(measure === 'in') delta *= 2.54
    manage_room.set_level_delta(delta)
    interior.from_interior(manage_room)
    manage_room.clear()
    store.recalc()
  }

  return (
    <Back>
      <TopMenu>
        <CloseBtn onClick={on_close} />
        <MenuTitle>{lang.mr.title}</MenuTitle>
        <NextBtn onClick={on_next}>
          <NextBtnText>{lang.mr.next}</NextBtnText>
          <NextBtnIcon />
        </NextBtn>
      </TopMenu>
      <Container ref={ref}>
        {manage_room.size.is_valid && (
          <>
            <Svg onDirty={on_dirty} />
            {is_help && (<Help>{lang.mr.help}</Help>)}
          </>
        )}
      </Container>
      {is_height && (
        <HeightModal onClose={on_height_close} onDone={on_height_done} />
      )}
    </Back>
  )
}

export const ManageRoom = observer(ManageRoomBase)
