import {useState} from 'react'
import {observer} from 'mobx-react-lite'

import {
  Svg as SvgLayout,
  PicControl,
  Resize,
  Tool,
  Picture,
  OwnBack
} from './layout'
import {Image} from './Image'
import {store} from 'store'

import info_btn_svg from 'assets/info_btn.svg'
import edit_btn_svg from 'assets/edit_btn.svg'
import delete_btn_svg from 'assets/delete_btn.svg'

const RT = 'RT', RB = 'RB', LB = 'LB', LT = 'LT'

const SvgBase = ({onSize}) => {
  const {interior, selected, own} = store
  const {cover, contain, size} = interior
  const [drag, setDrag] = useState()
  const [move, setMove] = useState()
  const [resize, setResize] = useState()

  const mouse_down = (e, picture) => {
    setDrag({
      x: e.clientX,
      y: e.clientY,
      picture
    })
  }

  const mouse_move = e => {
    if(drag) {
      setMove({
        x: e.clientX,
        y: e.clientY
      })
    }
    if(resize) {
      const
        mouse_x = e.clientX,
        mouse_y = e.clientY
      const {picture, type, x, y, offset, size} = resize
      const r = size.width / size.height
      let d_x = mouse_x - x, d_y = mouse_y - y
      if(type === RB) {
        if(d_x / d_y > r) d_x = d_y * r
        if(d_x / d_y < r) d_y = d_x / r
        const n_size = {
          width: size.width + d_x,
          height: size.height + d_y
        }
        const n_offset = {
          x: offset.x,
          y: offset.y
        }
        store.resize_drag(picture, n_size, n_offset)
      }
      if(type === LB) {
        if(-d_x / d_y > r) d_x = -d_y * r
        if(-d_x / d_y < r) d_y = -d_x / r
        const n_size = {
          width: size.width - d_x,
          height: size.height + d_y
        }
        const n_offset = {
          x: offset.x + d_x,
          y: offset.y
        }
        store.resize_drag(picture, n_size, n_offset)
      }
      if(type === LT) {
        if(d_x / d_y > r) d_x = d_y * r
        if(d_x / d_y < r) d_y = d_x / r
        const n_size = {
          width: size.width - d_x,
          height: size.height - d_y
        }
        const n_offset = {
          x: offset.x + d_x,
          y: offset.y + d_y
        }
        store.resize_drag(picture, n_size, n_offset)
      }
      if(type === RT) {
        if(d_x / -d_y > r) d_x = -d_y * r
        if(d_x / -d_y < r) d_y = -d_x / r
        const n_size = {
          width: size.width + d_x,
          height: size.height - d_y
        }
        const n_offset = {
          x: offset.x,
          y: offset.y + d_y
        }
        store.resize_drag(picture, n_size, n_offset)
      }
    }
  }

  const mouse_up = () => {
    if(drag && move) {
      const
        x = drag.picture.px_offset.x + move.x - drag.x,
        y = drag.picture.px_offset.y + move.y - drag.y
      store.set_picture_offset(drag.picture, {x, y})
      setDrag()
      setMove()
    }
    if(resize) setResize()
  }

  const get_drag_x = picture => {
    if(!drag || !move || picture !== drag.picture) return 0
    return move.x - drag.x
  }

  const get_drag_y = picture => {
    if(!drag || !move || picture !== drag.picture) return 0
    return move.y - drag.y
  }

  const edit_pic = picture => {
    onSize(picture)
  }

  const info_pic = () => {}

  const del_pic = picture => {
    store.remove_from_galery(picture)
  }

  const del_own = picture => {
    store.remove_own(picture)
  }

  const is_resize = () => {
    return resize !== null && resize !== undefined
  }

  const resize_down = (e, picture, type) => {
    setResize({
      picture,
      type,
      x: e.clientX,
      y: e.clientY,
      offset: {
        x: picture.px_offset.x,
        y: picture.px_offset.y
      },
      size: {
        width: picture.px_size.width,
        height: picture.px_size.height
      }
    })
  }

  if(!interior.is_valid) return null

  return (
    <SvgLayout
      width={size.width}
      height={size.height}
      viewBox={`0 0 ${size.width} ${size.height}`}
      xmlns="http://www.w3.org/2000/svg"
      onMouseMove={mouse_move}
      onMouseUp={mouse_up}
    >
      <defs>
        <filter id="blur">
          <feGaussianBlur stdDeviation="20" />
        </filter>
        <filter id="shadow">
          <feDropShadow dx="0" dy="0" stdDeviation="5" floodColor="#000000" floodOpacity="0.4" />
        </filter>
      </defs>
      <Image source={cover} blur />
      <rect x="0" y="0" width={size.width} height={size.height} fill="#FFFFFF" fillOpacity={0.5} />
      <Image source={contain} />
      {selected.map(picture => (
        <PicControl key={`selected_${picture.id}`} transform={`translate(${get_drag_x(picture)}, ${get_drag_y(picture)})`} className={`pic_hover`}>
          <rect x={picture.px_offset.x} y={picture.px_offset.y} width={picture.px_size.width} height={picture.px_size.height} fill="#FFFFFF" />
          <Picture href={picture.img.src} x={picture.px_offset.x} y={picture.px_offset.y} width={picture.px_size.width} height={picture.px_size.height} filter="url(#shadow)" onMouseDown={e => mouse_down(e, picture)} />
          <Tool onClick={() => info_pic(picture)} href={info_btn_svg} x={picture.px_offset.x + picture.px_size.width - 100} y={picture.px_offset.y + 16} width="36" height="36" />
          <Tool onClick={() => del_pic(picture)} href={delete_btn_svg} x={picture.px_offset.x + picture.px_size.width - 52} y={picture.px_offset.y + 16} width="36" height="36" />
        </PicControl>
      ))}
      {own.map(picture => (
        <PicControl key={`selected_${picture.id}`} transform={`translate(${get_drag_x(picture)}, ${get_drag_y(picture)})`} className={`pic_hover ${is_resize(picture) ? 'pic_drag' : ''}`}>
          <OwnBack x={picture.px_offset.x - 3} y={picture.px_offset.y - 3} width={picture.px_size.width + 6} height={picture.px_size.height + 6} />
          <Picture href={picture.img.src} x={picture.px_offset.x} y={picture.px_offset.y} width={picture.px_size.width} height={picture.px_size.height} filter="url(#shadow)" onMouseDown={e => mouse_down(e, picture)} />
          <Tool onClick={() => edit_pic(picture)} href={edit_btn_svg} x={picture.px_offset.x + picture.px_size.width - 100} y={picture.px_offset.y + 16} width="36" height="36" />
          <Tool onClick={() => del_own(picture)} href={delete_btn_svg} x={picture.px_offset.x + picture.px_size.width - 52} y={picture.px_offset.y + 16} width="36" height="36" />
          <Resize cx={picture.px_offset.x + picture.px_size.width} cy={picture.px_offset.y + picture.px_size.height} r="6" fill="#FFFFFF" onMouseDown={e => resize_down(e, picture, RB)} />
          <Resize cx={picture.px_offset.x} cy={picture.px_offset.y + picture.px_size.height} r="6" fill="#FFFFFF" onMouseDown={e => resize_down(e, picture, LB)} />
          <Resize cx={picture.px_offset.x} cy={picture.px_offset.y} r="6" fill="#FFFFFF" onMouseDown={e => resize_down(e, picture, LT)} />
          <Resize cx={picture.px_offset.x + picture.px_size.width} cy={picture.px_offset.y} r="6" fill="#FFFFFF" onMouseDown={e => resize_down(e, picture, RT)} />
        </PicControl>
      ))}
    </SvgLayout>
  )
}

export const Svg = observer(SvgBase)
