import {observer} from 'mobx-react-lite'

import {
  Container,
  Header,
  Title,
  CloseBtn,
  Body,
  Img
} from './layout'
import {store} from 'store'

const GaleryBase = ({onClose, onChoose}) => {
  const {lang, galery} = store

  return (
    <Container>
      <Header>
        <Title>{lang.galery.title}</Title>
        <CloseBtn onClick={onClose}/>
      </Header>
      <Body>
        {galery.map(picture => (
          <Img src={picture.img.src} key={`galery_${picture.id}`} onClick={() => onChoose(picture)} />
        ))}
      </Body>
    </Container>
  )
}

export const Galery = observer(GaleryBase)
