import {useState, useEffect, useRef} from 'react'
import {observer} from 'mobx-react-lite'

import {Container} from './Container'
import {GlobalStyle} from './layout'
import {TopMenu} from 'mobile/TopMenu'
import {Tooltip} from 'mobile/Tooltip'
import {BottomMenu} from 'mobile/BottomMenu'
import {MoreMenu} from 'mobile/MoreMenu'
import {ManageRoom} from 'mobile/ManageRoom'
import {Svg} from './Svg'
import {store} from 'store'

const MobileAppBase = () => {
  const [tooltip, setTooltip] = useState(true)
  const [more, setMore] = useState(false)
  const {manage_room} = store

  useEffect(() => {
    const cur_lang = new URLSearchParams(document.location.search).get('lang')
    if(cur_lang) {
      store.set_lang(cur_lang)
    }
  }, [])

  const edit_room = () => {
    store.manage_exist_room()
  }

  return (
    <>
      <GlobalStyle />
      <Container>
        <TopMenu onMore={() => setMore(true)} />
        {tooltip && <Tooltip onClose={() => setTooltip(false)} />}
        <Svg onSize={picture => {}} />
        <BottomMenu />
        {more && <MoreMenu onClose={() => setMore(false)} onManage={edit_room} />}
        {manage_room.img.is_valid && <ManageRoom />}
      </Container>
    </>
  )
}

export const MobileApp = observer(MobileAppBase)
