import {makeObservable, observable, action, computed, runInAction} from 'mobx'

import {lang as en_lang} from 'lang/en'
import {lang as ru_lang} from 'lang/ru'
import back_png from 'assets/back.png'
import mob_back_png from 'assets/mob_back.png'

const
  CEIL_REL = 0.3, FLOOR_REL = 0.7,
  HEIGHT = 320, WIDTH = 600,
  MOB_HEIGHT = 320, MOB_WIDTH = 150

class Size {
  width
  height

  constructor() {
    this.width = null
    this.height = null

    makeObservable(this, {
      width: observable,
      height: observable,
      is_valid: computed
    })
  }

  get is_valid() {
    return !isNaN(parseFloat(this.width)) && !isNaN(parseFloat(this.height))
  }
}

export class Img {
  src
  size

  constructor() {
    this.size = new Size()
    this.src = null

    makeObservable(this, {
      src: observable,
      size: observable,
      is_valid: computed
    })
  }

  get is_valid() {
    return (
      this.size !== null &&
      this.size !== undefined &&
      this.size.is_valid &&
      this.src !== null &&
      this.src !== undefined
    )
  }
}

class Offset {
  x
  y

  constructor() {
    this.x = null
    this.y = null

    makeObservable(this, {
      x: observable,
      y: observable,
      is_valid: computed
    })
  }

  get is_valid() {
    return !isNaN(parseFloat(this.x)) && !isNaN(parseFloat(this.y))
  }
}

class ImgDisplay {
  offset
  img

  constructor() {
    this.offset = new Offset()
    this.img = new Img()
  }

  get is_valid() {
    return this.offset.is_valid && this.img.is_valid
  }
}

export class Level {
  px_ceil
  px_floor
  cm_delta
  cm_height
  cm_width

  constructor() {
    this.px_ceil = null
    this.px_floor = null
    this.cm_delta = null
    this.cm_height = null
    this.cm_width = null

    makeObservable(this, {
      px_ceil: observable,
      px_floor: observable,
      cm_delta: observable,
      cm_height: observable,
      cm_width: observable,
      is_valid: computed,
      inch_delta: computed
    })
  }

  get is_valid() {
    return (
      !isNaN(parseFloat(this.px_ceil)) &&
      !isNaN(parseFloat(this.px_floor)) &&
      !isNaN(parseFloat(this.cm_delta)) &&
      !isNaN(parseFloat(this.cm_height)) &&
      !isNaN(parseFloat(this.cm_width))
    )
  }

  get inch_delta() {
    return this.cm_delta / 2.54
  }
}

class Interior {
  size
  img
  level

  constructor() {
    this.size = new Size()
    this.img = new Img()
    this.level = new Level()

    makeObservable(this, {
      size: observable,
      img: observable,
      level: observable,
      setSize: action,
      setWidth: action,
      setHeight: action,
      setImgBySrc: action,
      setDefaultImg: action,
      setForLevel: action,
      clear: action,
      set_level_ceil: action,
      set_level_floor: action,
      set_level_delta: action,
      set_level_size: action,
      from_interior: action,
      cover: computed,
      contain: computed,
      is_valid: computed,
      level_ceil: computed,
      level_floor: computed
    })
  }

  get is_valid() {
    return (
      this.size.is_valid &&
      this.img.is_valid &&
      this.level.is_valid
    )
  }

  setSize({width, height}) {
    this.size.width = width
    this.size.height = height
  }

  setWidth(width) {
    this.size.width = width
  }

  setHeight(height) {
    this.size.height = height
  }

  setImgBySrc(src) {
    const img = new Image()
    img.onload = () => {
      runInAction(() => {
        this.img.size.width = img.width
        this.img.size.height = img.height
        this.img.src = src
      })
    }
    img.src = src
  }

  setDefaultImg(img, level) {
    if(!img.is_valid || !level.is_valid) return
    this.img.size.width = img.size.width
    this.img.size.height = img.size.height
    this.img.src = img.src
    this.level.px_ceil = level.px_ceil
    this.level.px_floor = level.px_floor
    this.level.cm_height = level.cm_height
    this.level.cm_width = level.cm_width
    this.level.cm_delta = level.cm_delta
  }

  setForLevel(src) {
    const image = new Image()
    image.onload = () => {
      runInAction(() => {
        this.img.src = src
        this.img.size.width = image.width
        this.img.size.height = image.height
        this.level.px_ceil = image.height * 0.3
        this.level.px_floor = image.height * 0.7
      })
    }
    image.src = src
  }

  clear() {
    this.img.src = null
    this.img.size.width = null
    this.img.size.height = null
    this.size.width = null
    this.size.height = null
    this.level.px_ceil = null
    this.level.px_floor = null
    this.level.cm_width = null
    this.level.cm_height = null
  }

  set_level_ceil(ceil) {
    this.level.px_ceil = ceil * this.img.size.height / this.contain.img.size.height
  }

  set_level_floor(floor) {
    this.level.px_floor = floor * this.img.size.height / this.contain.img.size.height
  }

  set_level_delta(delta) {
    this.level.cm_delta = delta
    const ratio = delta / (this.level.px_floor - this.level.px_ceil)
    this.level.cm_height = this.img.size.height * ratio
    this.level.cm_width = this.img.size.width * ratio
  }

  set_level_size(width, height) {
    this.size.width = width
    this.size.height = height
  }

  from_interior(interior) {
    this.img.src = interior.img.src
    this.img.size.width = interior.img.size.width
    this.img.size.height = interior.img.size.height
    this.level.px_ceil = interior.level.px_ceil
    this.level.px_floor = interior.level.px_floor
    this.level.cm_height = interior.level.cm_height
    this.level.cm_width = interior.level.cm_width
    this.level.cm_delta = interior.level.cm_delta
  }

  get level_floor() {
    return this.level.px_floor * this.contain.img.size.height / this.img.size.height
  }

  get level_ceil() {
    return this.level.px_ceil * this.contain.img.size.height / this.img.size.height
  }

  calc(fn) {
    const display = new ImgDisplay()
    const hrel = this.size.width / this.img.size.width
    const vrel = this.size.height / this.img.size.height
    const rel = fn(hrel, vrel)
    display.offset.x = (this.size.width - this.img.size.width * rel) / 2
    display.offset.y = (this.size.height - this.img.size.height * rel) / 2
    display.img.size.width = this.img.size.width * rel
    display.img.size.height = this.img.size.height * rel
    display.img.src = this.img.src
    return display
  }

  get cover() {
    if(!this.size.is_valid || !this.img.is_valid) return null
    return this.calc(Math.max)
  }

  get contain() {
    if(!this.size.is_valid || !this.img.is_valid) return null
    return this.calc(Math.min)
  }
}

export class Picture {
  id
  img
  cm_size
  cm_offset
  px_size
  px_offset

  constructor(id, img) {
    this.id = id
    this.img = img
    this.cm_size = new Size()
    this.cm_offset = new Offset()
    this.px_size = new Size()
    this.px_offset = new Offset()

    makeObservable(this, {
      img: observable,
      cm_size: observable,
      cm_offset: observable,
      px_size: observable,
      px_offset: observable,
      is_valid: computed,
      ratio: computed
    })
  }

  get is_valid() {
    return (
      this.img.is_valid &&
      this.cm_size.is_valid &&
      this.cm_offset.is_valid &&
      this.px_size.is_valid &&
      this.px_offset.is_valid
    )
  }

  get ratio() {
    return this.img.size.width / this.img.size.height
  }
}

export class AppStore {
  interior
  own
  selected
  galery
  own_id
  lang
  manage_room
  measure

  constructor() {
    this.interior = new Interior()
    this.own = []
    this.selected = []
    this.galery = []
    this.own_id = 100
    this.lang = null
    this.measure = 'cm'
    this.manage_room = new Interior()

    makeObservable(this, {
      own: observable,
      selected: observable,
      own_id: observable,
      lang: observable,
      measure: observable,
      select_from_galery: action,
      center_picture: action,
      first_from_galary: action,
      set_picture_offset: action,
      recalc_size: action,
      recalc: action,
      remove_from_galery: action,
      manage_exist_room: action,
      set_measure: action,
      set_desktop_default: action,
      set_mobile_default: action,
      upload_pic: action,
      remove_own: action,
      resize_pic: action,
      resize_drag: action,
      set_lang: action
    })
  }

  recalc_picture(picture) {
    if(!this.interior.img.is_valid) return
    const contain = this.interior.contain
    const w_ratio = this.interior.level.cm_width / contain.img.size.width
    const h_ratio = this.interior.level.cm_height / contain.img.size.height
    const {cm_size, cm_offset} = picture
    picture.px_size.width = cm_size.width / w_ratio
    picture.px_size.height = cm_size.height / h_ratio
    picture.px_offset.x = cm_offset.x / w_ratio + contain.offset.x
    picture.px_offset.y = cm_offset.y / h_ratio + contain.offset.y
  }

  set_picture_offset(picture, offset) {
    if(!this.interior.img.is_valid) return
    const contain = this.interior.contain
    const w_ratio = this.interior.level.cm_width / contain.img.size.width
    const h_ratio = this.interior.level.cm_height / contain.img.size.height
    picture.px_offset.x = offset.x
    picture.px_offset.y = offset.y
    picture.cm_offset.x = (offset.x - contain.offset.x) * w_ratio
    picture.cm_offset.y = (offset.y - contain.offset.y) * h_ratio
  }

  recalc_size(size) {
    this.interior.setSize(size)
    this.recalc()
  }

  recalc() {
    this.own.forEach(picture => {
      this.center_picture(picture)
      this.recalc_picture(picture)
    })
    this.selected.forEach(picture => {
      this.center_picture(picture)
      this.recalc_picture(picture)
    })
  }

  select_from_galery(galary_img) {
    const {id, cm_size} = galary_img
    const img = new Img()
    img.size.width = galary_img.img.width
    img.size.height = galary_img.img.height
    img.src = galary_img.img.src
    const picture = new Picture(id, img)
    picture.cm_size.width = cm_size.width
    picture.cm_size.height = cm_size.height
    this.center_picture(picture)
    this.recalc_picture(picture)
    this.selected.push(picture)
  }

  remove_from_galery(picture) {
    const idx = this.selected.indexOf(picture)
    if(idx < 0) return
    this.selected.splice(idx, 1)
  }

  remove_own(picture) {
    const idx = this.own.indexOf(picture)
    if(idx < 0) return
    this.own.splice(idx, 1)
  }

  center_picture(picture) {
    picture.cm_offset.x = this.interior.level.cm_width / 2 - picture.cm_size.width / 2
    picture.cm_offset.y = this.interior.level.cm_height / 2 - picture.cm_size.height / 2
  }

  first_from_galary() {
    if(this.galery.length <= 0) return
    const galary_img = this.galery[0]
    this.select_from_galery(galary_img)
  }

  manage_exist_room() {
    this.manage_room.from_interior(this.interior)
  }

  set_measure(measure) {
    this.measure = measure
  }

  set_desktop_default(callback) {
    const image = new Image()
    image.onload = () => {
      runInAction(() => {
        const img = new Img()
        img.src = back_png
        img.size.width = image.width
        img.size.height = image.height
        const level = new Level()
        level.px_ceil = img.size.height * CEIL_REL
        level.px_floor = img.size.height * FLOOR_REL
        level.cm_height = HEIGHT
        level.cm_width = WIDTH
        level.cm_delta = Math.round(HEIGHT * (FLOOR_REL - CEIL_REL))
        this.interior.setDefaultImg(img, level)
        if(callback) callback()
      })
    }
    image.src = back_png
  }

  set_mobile_default(callback) {
    const image = new Image()
    image.onload = () => {
      runInAction(() => {
        const img = new Img()
        img.src = mob_back_png
        img.size.width = image.width
        img.size.height = image.height
        const level = new Level()
        level.px_ceil = img.size.height * CEIL_REL
        level.px_floor = img.size.height * FLOOR_REL
        level.cm_height = MOB_HEIGHT
        level.cm_width = MOB_WIDTH
        level.cm_delta = Math.round(MOB_HEIGHT * (FLOOR_REL - CEIL_REL))
        this.interior.setDefaultImg(img, level)
        if(callback) callback()
      })
    }
    image.src = mob_back_png
  }

  upload_pic(src) {
    const image = new Image()
    image.onload = () => {
      runInAction(() => {
        const img = new Img()
        img.size.width = image.width
        img.size.height = image.height
        img.src = src
        const id = this.own_id++
        const picture = new Picture(id, img)
        const r = image.width / image.height
        const height = Math.round((this.interior.level.cm_height / 4 + Number.EPSILON) * 100) / 100
        const width = Math.round((height * r + Number.EPSILON) * 100) / 100
        picture.cm_size.width = width
        picture.cm_size.height = height
        this.center_picture(picture)
        this.recalc_picture(picture)
        this.own.push(picture)
      })
    }
    image.src = src
  }

  resize_pic(picture, size) {
    if(!this.interior.img.is_valid) return
    const contain = this.interior.contain
    const w_ratio = this.interior.level.cm_width / contain.img.size.width
    const h_ratio = this.interior.level.cm_height / contain.img.size.height
    const
      old_width = picture.cm_size.width,
      old_height = picture.cm_size.height
    picture.cm_size.width = size.width
    picture.cm_size.height = size.height
    picture.px_size.width = size.width / w_ratio
    picture.px_size.height = size.height / h_ratio
    picture.cm_offset.x += (old_width - size.width) / 2
    picture.cm_offset.y += (old_height - size.height) / 2
    picture.px_offset.x = picture.cm_offset.x / w_ratio + contain.offset.x
    picture.px_offset.y = picture.cm_offset.y / h_ratio + contain.offset.y
  }

  resize_drag(picture, size, offset) {
    if(!this.interior.img.is_valid) return
    const contain = this.interior.contain
    const w_ratio = this.interior.level.cm_width / contain.img.size.width
    const h_ratio = this.interior.level.cm_height / contain.img.size.height
    picture.px_size.width = size.width
    picture.px_size.height = size.height
    picture.px_offset.x = offset.x
    picture.px_offset.y = offset.y
    const width = Math.round((size.width * w_ratio + Number.EPSILON) * 100) / 100
    const height = Math.round((size.height * h_ratio + Number.EPSILON) * 100) / 100
    picture.cm_size.width = width
    picture.cm_size.height = height
    picture.cm_offset.x = (offset.x - contain.offset.x) * w_ratio
    picture.cm_offset.y = (offset.y - contain.offset.y) * h_ratio
  }

  set_lang(lang) {
    if(lang === 'ru') this.lang = ru_lang
    if(lang === 'en') this.lang = en_lang
  }
}
