import {observer} from 'mobx-react-lite'

import {store} from 'store'
import {
  Back,
  Container,
  Body,
  Item,
  ShareItem,
  ManageIcon,
  RemoveIcon,
  ShareIcon,
  Text
} from './layout'

const MoreMenuBase = ({onManage, onRemove, onShare, onClose}) => {
  const {lang: {mob: {more}}} = store

  return (
    <Back onClick={onClose}>
      <Container>
        <Body>
          <Item onClick={onManage}><ManageIcon /><Text>{more.manage}</Text></Item>
          <Item onClick={onRemove}><RemoveIcon /><Text>{more.remove}</Text></Item>
          <ShareItem onClick={onShare}><ShareIcon /><Text>{more.share}</Text></ShareItem>
        </Body>
      </Container>
    </Back>
  )
}

export const MoreMenu = observer(MoreMenuBase)
